var ShadowScroll = {
  setScroll() {
    var scroll = $(window).scrollTop();
    var wheight = $(window).height();
    var wpercents = scroll / (wheight / 100);
    var opacity = (wpercents / 100) * 1.5;
    opacity > 1 ? opacity = 1 : '';
    var $curtain = $('.js-curtain');
    $curtain.css({
      'background-color': `rgba(0, 0, 0, ${opacity})`
    });
  },
  init() {
    var t = this;
    t.setScroll();
    $(window).on('scroll resize', (e) => {
      t.setScroll();
    })
  }
}

var ShowScroll = {
  onScroll() {
    var scroll = $(window).scrollTop();
    var wheight = $(window).height();
    var scrollBottom = scroll + wheight;
    $('.js-show').each(function() {
      var $this = $(this);
      var center = $this.offset().top + $this.outerHeight()/2;
      if(center < scrollBottom) {
        $this.addClass('isShown');
      } else {
        $this.removeClass('isShown');
      }
    });
  },
  init() {
    var t = this;
    t.onScroll();
    $(window).on('scroll resize', (e) => {
      t.onScroll();
    })
  }
}

$(function(){
  ShadowScroll.init();
  ShowScroll.init();
});
